@import "~@carbon/themes/scss/themes";
$carbon--theme: $carbon--theme--g90;
@include carbon--theme();

.CatalogCourseView {
    position: relative;

    .markdown-body {
        color: $text-01;
    }

    @media (min-width: 1056px) {
        .CatalogCourseView__table-of-contents {
            width: 20%;
            position: absolute;
            right: 0;
        }

        .CatalogCourseView__content {
            width: 75%;
            float: left;

            .Content__content {
                // transparent scrollbar for content
                &::-webkit-scrollbar-thumb,
                &::-webkit-scrollbar-track {
                    background: transparent;
                }

                .Content__section {
                    .Content__section-header {
                        font-size: 1.25em;
                        margin-bottom: 1rem;
                        cursor: pointer;
                    }
    
                    .markdown-body {
                        img {
                            max-width: 100%;
                            box-sizing: initial;
                            background-color: #fff;
                            display: block;
                        }
                    }
                }    
            }
        }
    }
}
