.Demos {
  .Demos__info {
    .Demos__description {
      margin-bottom: 2rem;
    }  
  }

  .Demos__tiles {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-gap: 2px;  

    // font sizes and padding sizes are fixed by specification; so using px instead of em or rem
    .Demos__tile {
      height: 250px;
      padding: 24px 16px;

      .Demos__tile-title {
        line-height: 24px;
        font-size: 17px;
        height: 72px;
        overflow: hidden;
        margin-bottom: 6px;
      }

      .Demos__tile-details {
        position: relative;

        .description {
          overflow: hidden;
          line-height: 20px;
          font-size: 14px;
          height: 80px;
          margin-bottom: 24px;
        }

        .icon {
          float: right;
          fill: currentColor;
        }
      }
    }  
  }
  
  &.small {

  }

  &.medium {

  }
  
  &.large {
    .Demos__info {
      display: inline-block;
      width: 25%;
      margin-right: 1rem;

      .Demos__title {
        font-weight: bold;
        margin-bottom: 1rem;
      }
    }

    .Demos__tiles {
      display: inline-grid;
      width: calc(75% - 1rem);
      vertical-align: top;
    }
  }
}
