.Tutorials {
  .Tutorials__info {
    .Tutorials__description {
      margin-bottom: 2rem;
    }
  }

  .Tutorials__course-tiles {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-gap: 3px;  

    // font sizes and padding sizes are fixed by specification; so using px instead of em or rem
    .Tutorials__course-tile {
      height: 250px;
      padding: 24px 16px;

      .Tutorials__course-tile-title {
        line-height: 24px;
        font-size: 17px;
        height: 40px;
        overflow: hidden;
        margin-bottom: 10px;
        // overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .Tutorials__course-tile-details {
        position: relative;

        .description {
          overflow-y: auto;
          line-height: 20px;
          font-size: 14px;
          height: 110px;
          margin-bottom: 24px;
          white-space: break-spaces;
          overflow-x: hidden;
        }

        .duration {
          display: inline-block;
          line-height: 16px;
          font-size: 14px;
          height: 16px;

          .duration-icon {
            fill: currentColor;
            margin-right: 8px;
            vertical-align: middle;
          }
        }

        .icon {
          float: right;
          fill: currentColor;
        }
      }
    }  
  }
  
  &.small {

  }

  &.medium {

  }
  
  &.large {
    .Tutorials__info {
      display: inline-block;
      width: 25%;
      margin-right: 1rem;

      .Tutorials__title {
        font-weight: bold;
        margin-bottom: 1rem;
      }
    }

    .Tutorials__course-tiles {
      display: inline-grid;
      width: calc(75% - 1rem);
      vertical-align: top;
    }
  }
}
