.TableOfContents {
  font-size: 1.125em;

  .TableOfContents__title {
    padding-left: 1rem;
    margin: 1.5rem 0;
  }

  .TableOfContents__nav {
    .TableOfContents__nav-item {
      min-height: 2.5rem;
      padding: 0.875rem 0 0.875rem 1rem;
      border-left: 4px solid transparent;

      .TableOfContents__nav-link {
        font-weight: bold;
        color: #bfbfbf;
        text-decoration: none;
      }

      &.TableOfContents__nav-item--selected {
        background-color: #3d3d3d;
        border-left: 4px solid #025ff4;

        .TableOfContents__nav-link {
          color: #e9e9e9;
        }
      }
    }
  }
}
