:root {
  --landscape-width-left: 15; // % OR vw for responsive window resizing!
  --landscape-width-right: 40;
  --portrait-width: 15;
  --header-height: 2.5rem;
  --secs: 0.4s;
  --main-min-width: 45;
  --main-max-width: 55;
  --landscape-width-right-at50: 45;
}

body {
  min-height: 100vh;
  max-width: 100vw;
  margin: 0;
  // overflow: hidden; // Disable scrolling on body
}

.app_container {
  .header_homeLink {
    text-decoration: none;
    color: #f1f1f1;
  }

  @media (max-width: 66rem) {
    .bx--side-nav--ux {
      width: auto !important;
    }
  }

  .bx--header__menu-toggle__hidden {
    display: block !important;
  }

  .bx--header {
    position: inherit;
  }

  .bx--header__action {
    width: auto;
    min-width: 3rem;
    color: #fff;
  }

  .breadcrumb {
    position: absolute;
    left: 14px;
    line-height: 1.56rem;

    .bx--breadcrumb-item .bx--link {
      color: #0f62fe;
    }

    .bx--breadcrumb-item.bx--breadcrumb-item--current .bx--link {
      color: #161616 !important;
      cursor: auto;
    }
  }
  /********** ********************/

  .bx--header {
    z-index: 9999;
    .sideNav {
      .bx--side-nav__items {
        padding: 0rem 0 0;
      }

      background-color: #161616;
      color: #f4f4f4;
      .bx--side-nav__link-text > a,
      .bx--side-nav__submenu,
      .bx--side-nav__link-text {
        color: #f4f4f4;
        .bx--side-nav__icon > svg {
          fill: #f4f4f4;
        }
      }
      .bx--side-nav__item:not(.bx--side-nav__item--active) > .bx--side-nav__link:hover,
      .bx--side-nav__submenu:hover,
      .bx--side-nav__menu[role="menu"]
        a.bx--side-nav__link[role="menuitem"]:not(.bx--side-nav__link--current):not([aria-current="page"]):hover
        .bx--side-nav__item:not(.bx--side-nav__item--active)
        .bx--side-nav__menu-item
        > .bx--side-nav__link:hover
        > span {
        background-color: #2c2c2c;
        color: #f4f4f4;
        font-weight: 100;
      }

      .bx--side-nav__item:hover {
        background-color: #2c2c2c;
      }

      .bx--side-nav__item:not(.bx--side-nav__item--active) .bx--side-nav__menu-item > .bx--side-nav__link:hover > span {
        // background-color: #2c2c2c;
        color: #ffffff;
      }

      .bx--side-nav__menu[role="menu"]
        a.bx--side-nav__link[role="menuitem"]:not(.bx--side-nav__link--current):not([aria-current="page"]):hover {
        background-color: #2c2c2c;
        font: inherit;
        font-weight: 100;
      }

      .bx--side-nav__menu[role="menu"] a.bx--side-nav__link--current,
      a.bx--side-nav__link--current {
        background-color: #2c2c2c;
        font-weight: 600;
      }

      .bx--side-nav__item:not(.bx--side-nav__item--active) > .bx--side-nav__link:hover > span,
      .bx--side-nav__item:not(.bx--side-nav__item--active) .bx--side-nav__menu-item > .bx--side-nav__link:hover > span {
        color: #f4f4f4;
      }

      .bx--side-nav__navigation.bx--side-nav.sideNav.bx--side-nav--ux > ul > div > a:nth-child(n) > li > a {
        font-weight: 100;
      }

      .bx--side-nav a.bx--header__menu-item,
      a.bx--side-nav__link {
        font-weight: 100;
      }
      a.bx--side-nav__link--current:before {
        background-color: #000;
      }
    }
    .subItem {
      text-decoration: none;
    }
  }

  .toggle-nav {
    background-color: #161616;
    width: 4em;
    height: 3.5em;
    border: black;
    cursor: pointer;
  }

  .icon-fill {
    fill: #ffffff;
  }

  button.toggle-nav:hover {
    border: white;
  }

  @media (min-width: 66rem) {
    .bx--offset-lg-2 {
      margin-left: 0px;
    }
  }

  #user-menu--icon {
    display: flex;
    align-items: center;
    color: #fff;
    width: auto;
    padding-right: 1rem;
    padding-left: 1rem;

    .nav--item-account {
      align-items: center;
      justify-content: center;
      display: flex;
    }

    svg {
      fill: #fff;
    }

    span#user-menu--name {
      padding-left: 0.5rem;
    }
  }
  /************ ********************/

  .layout_header {
    position: relative;
    height: var(--header-height);
    background-color: #f0f2f3;
    border: 1px solid #d9d9d9;
    padding: 6px;

    .metadataTag {
      display: flex;

      .marklabcomplete {
        cursor: pointer;
        color: #0f62fe;
        line-height: 24px;
        margin-right: 6px;
      }

      .labcompleted {
        color: #0f62fe;
        line-height: 24px;
        margin-right: 15px;
        display: flex;

        svg {
          width: 22px;
          height: 24px;
          margin-right: 5px;
        }
      }
    }

    .bx--breadcrumb-item--current::after {
      content: none;
    }

    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      overflow: hidden;
    }

    li {
      float: left;
    }

    li a,
    .dropbtn {
      display: inline-block;
      color: white;
      text-align: center;
      text-decoration: none;
    }

    li.dropdown {
      display: inline-block;
    }

    .dropdown-content {
      display: none;
      min-width: 160px;
      z-index: 1000;
      position: absolute;
      right: 0px;
      background-color: #fff;
      top: 28px;
    }

    .dropdown_item {
      padding: 10px;
    }

    .outline_toggler {
      float: right;
    }

    .bx--toggle-input__label .bx--toggle__switch {
      margin-top: 0rem !important;
    }

    .dropdown-content li {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      text-align: left;
    }

    .toggle_margin {
      margin-right: 5px;
    }

    .dropdown-content li:hover {
      background-color: #f1f1f1;
    }

    .dropdown:hover .dropdown-content {
      display: block;
    }

    .action-bar {
      display: flex;
      justify-content: flex-end;

      .CourseContent__header-rightArrow,
      .CourseContent__header-leftArrow,
      .leftAction,
      .allAction,
      .rightAction {
        margin: 0 3px;
      }

      .svgStyle {
        width: 25px;
        height: 25px;
      }
    }

    .bx--col-lg-3 {
      text-align: right;
    }

    .bx--col-lg-1 {
      text-align: right;
    }
  }

  .courseOutline__header_title {
    font-size: 18px;
    margin-left: 10px;
    width: 70%;
    float: left;
  }

  .courseOutline__header_info {
    .courseOutline__timer {
      padding: 5px;
      font-size: 14px;
    }
  }

  #layout {
    display: flex; // Flexbox definition
    overflow: hidden; // Disable scrolling on layout

    .bx--skeleton__placeholder {
      width: 100%;
      height: calc(100vh - 48px - var(--header-height)); // subtract heights of both headers
    }

    // Style in common for sidebars and main area:
    .header {
      position: relative;
      height: var(--header-height);
      text-align: center; // Horizontal center
      .title {
        position: absolute;
        left: 0;
        right: 0;
      }
    }

    .sidebar-content {
      overflow: unset;
      box-sizing: border-box;
      height: calc(100vh - 120px); //120 is set so that bottom of the course outline is seen
    }
    .content {
      overflow: auto;
      box-sizing: border-box;
      height: calc(100vh - var(--header-height) - 3rem);
    }

    .CourseOutline__header {
      height: var(--header-height);
      font-size: 16px;
      font-weight: 600;
      text-align: left;
      padding: 10px;
      border-bottom: 1px solid darkgray;
    }
  }

  // Main area specific style:
  #main {
    flex-grow: 1;
    overflow-x: auto;
    @media (orientation: landscape) {
      &.openat50 {
        // min-width: calc(1% * var(--main-min-width));
        width: calc(1% * var(--main-max-width));
      }
    }

    @media (orientation: landscape) {
      &.open {
        min-width: calc(1% * var(--main-min-width));
      }
    }

    @media (orientation: portrait) {
      &.openat50 {
        // min-width: calc(1% * var(--main-min-width));
        width: calc(1% * var(--main-max-width));
      }
    }

    @media (orientation: portrait) {
      &.open {
        min-width: calc(1% * var(--main-min-width));
      }
    }

    @media (orientation: portrait) {
      &.closed {
        display: none;
      }
    }

    @media (orientation: landscape) {
      &.closed {
        display: none;
      }
    } // <-- Landscape ONLY sidebars
    .header {
      @media (orientation: landscape) {
        .title {
          transition: left var(--secs), right var(--secs);
          &.left-closed {
            left: var(--header-height);
          }
          &.right-closed {
            right: var(--header-height);
          }
        }
      }
    }
  }

  // Style in common for both sidebars:
  #left {
    transition: width var(--secs);
    width: 0;
    @media (orientation: landscape) {
      &.open {
        width: calc(1% * var(--landscape-width-left));
      }
    } // <-- Landscape ONLY sidebars
    @media (orientation: portrait) {
      &.open {
        width: calc(1% * var(--landscape-width-left));
      }
    } // <-- Landscape ONLY sidebars
    @media (orientation: landscape) {
      &.closed {
        display: none;
      }
    }
    .icon {
      position: fixed;
      z-index: 10;
      // width: var(--header-height);
      height: var(--header-height);
      line-height: var(--header-height);
      font-size: var(--header-height);
      text-align: center;
      user-select: none;
      cursor: pointer;
      border-bottom: 1px solid darkgray;
    }
    .sidebar {
      transition: transform var(--secs); // <-- Portrait AND landscape!
      @media (orientation: portrait) {
        width: calc(1vw * var(--portrait-width));
      }
      @media (orientation: landscape) {
        width: calc(1vw * var(--landscape-width-left));
      }
      @media (orientation: landscape) {
        &.closed {
          display: none;
        }
      }
      @media (orientation: portrait) {
        &.closed {
          display: none;
        }
      }
      // .header {
      //   // width: calc(100% - var(--header-height));
      // }
    }
  }

  #right {
    transition: width var(--secs);
    flex-grow: 1;
    transition: left var(--secs), right var(--secs);
    @media (orientation: landscape) {
      &.open {
        min-width: calc(1% * var(--landscape-width-right));
      }
    } // <-- Landscape ONLY sidebars
    @media (orientation: landscape) {
      &.openat50 {
        width: calc(1% * var(--landscape-width-right-at50));
      }
    }

    @media (orientation: portrait) {
      &.open {
        min-width: calc(1% * var(--landscape-width-right));
      }
    } // <-- Landscape ONLY sidebars
    @media (orientation: portrait) {
      &.openat50 {
        width: calc(1% * var(--landscape-width-right-at50));
      }
    }

    @media (orientation: landscape) {
      &.closed {
        display: none;
      }
    }

    .icon {
      position: fixed;
      z-index: 10;
      width: var(--header-height);
      height: var(--header-height);
      line-height: var(--header-height);
      font-size: var(--header-height);
      text-align: center;
      user-select: none;
      cursor: pointer;
      border: none;
    }

    .sidebar {
      transition: transform var(--secs); // <-- Portrait AND landscape!
      @media (orientation: portrait) {
        min-width: calc(1vw * var(--landscape-width-right));
      }

      @media (orientation: portrait) {
        &.closed {
          display: none;
        }
      }
      @media (orientation: landscape) {
        &.closed {
          display: none;
        }
      }

      .header {
        // setting height to zero; we're going to use tabs instead
        height: 0;

      }

      .content {
        position: relative;

        .Container__right-sidebar-tabs {
          background-color: #000;

          .bx--tabs__nav {
            width: 100%;

            .bx--tabs__nav-item {
              max-width: calc(25% - 12.5px); // (3 terminals + vnc)/4 - button

              &.bx--tabs__nav-item--selected {
                background-color: #1c1c1c; // same as terminal
              }

              .bx--tabs__nav-link {
                color: #7e7e7e;
                border-bottom: unset;

                &:focus,
                &:active {
                  outline: unset;
                }
              }
            }
          }
        }

        .Container__right-sidebar-tabs--content {
          padding: 0;
        }

        .Container__add-new-terminal {
          position: absolute;
          top: 0;
          right: 0;
          color: #7e7e7e;
          background-color: #1c1c1c;
          cursor: pointer;
          font-size: 30px;
          width: 40px;
          height: 40px;
          border: none;
        }
      }
    }
  }

  // Left sidebar specific style:
  #left {
    z-index: 5;
    .sidebar {
      background: var(--bg-left);
      &.closed {
        transform: translateX(-100%);
      } // <-- Left portrait AND landscape!
      .header {
        left: var(--header-height);
      }
    }
  }

  // Right sidebar specific style:
  #right {
    .icon {
      right: 0;
    }
    .sidebar {
      background: var(--bg-right);
    }
  }
}
