.CourseContent {
    flex: 1;
    // height: 100vh;
    overflow: auto;

    .quiz_container {
        font-family: inherit;
        .bx--grid {
            text-align: center;
        }
        .bx--inline-loading {
            width: unset;
        }

        .mylearning-results {
            display: none;
        }

        .bx--row {
            padding: 16px;
        }

        .quiz_title {
            font-size: 16px;
            font-weight: 600;
        }

        .quiz_subtitle {
            padding-top: 0px;
        }

        .padding_top0 {
            padding-top: 0px;
        }
        .padding_bottom0 {
            padding-bottom: 0px;
        }

        .buttonTextPadding {
            padding-left: 38px;
        }

        .quizNumbers {
            font-size: medium;
            font-weight: 600;
            display: flex;
            justify-content: center;
        }

        .borderRight1px {
            border-right: 1px solid;
        }

        .borderLeft1px {
            border-left: 1px solid;
        }

        .quizParams {
            font-size: smaller;
            font-weight: 400;
        }

        .quiz-ui .answers {
            font-size: 15px;
        }

        .quiz-ui h3 {
            font-size: 1rem;
            font-weight: 600;
        }

        .resultsNumbers {
            font-size: medium;
            font-weight: 700;
        }

        .messageFail,
        .messagePass {
            display: inline-block;
            color: #00f;
            font-weight: 500;
            font-size: medium;
        }

        progress {
            border-radius: 10px;
            height: 1.3rem;
            width: 20rem;
            padding-right: 0.5rem;
        }

        .star {
            filter: invert(86%) sepia(22%) saturate(4231%) hue-rotate(360deg) brightness(104%) contrast(103%);
        }

        // .hidden_div {
        //     display: none;
        // }
    }
    .learningIndex_container {
        .collpasibleSvg {
            position: absolute;
            right: 0px;
            padding-right: 12px;
        }

        .collpasibleSvg_down {
            display: block;
        }

        .collpasibleSvg_right {
            display: none;
        }

        .container {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
        }

        .item {
            width: 25%;
            // padding-bottom: 3%; /* Same as width, sets height */
            margin-bottom: 1%; /* (100-32*3)/2 */
            position: relative;
            background-color: #f1f1f1 !important;
            border: 1px solid #d9d9d9;
            padding: 10px;
        }

        .item a {
            text-decoration: none;
        }

        .categoryTitle {
            width: 100%;
            height: 2.5rem;
            background: #e0e0e0;
            padding: 8px;
            font-size: 16px;
            border: 1px solid #d9d9d9;
            margin-bottom: 5px;
            position: relative;
            font-weight: 600;
        }

        .documentHeader {
            text-align: center;
            padding: 10px;
            height: 2.5rem;
            background: #e0e0e0;
            font-weight: 600;
            margin-bottom: 10px;
        }
    }
    .Content__content {
        flex: 1;
        height: calc(100vh - 132px);
        overflow: auto;
        padding-top: 15px;
        text-align: left;
        padding: 18px;

        .zoomWrapper {
            display: flex;
            justify-content: flex-end;
            z-index: 700000;
        }

        .zoomToggle {
            height: 1rem;
            position: fixed;
            background-color: white;
            width: 32px;
            height: 32px;
            z-index: 700000;
        }

        .zoomToggle .zommin {
            cursor: pointer;
        }
        .zoomToggle .zommout {
            cursor: pointer;
        }
    }
    .bx--skeleton__placeholder {
        width: 100vw;
        height: 100vh;
    }
    .CourseContent__header {
        height: 44px;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        padding: 10px;
        padding-left: 16px;
        position: relative;
        padding-top: 20px;

        .CourseContent__header-leftArrow {
            left: 10px;
            position: absolute;
            bottom: 0px;
            cursor: pointer;
        }

        .CourseContent__header-rightArrow {
            right: 20px;
            position: absolute;
            bottom: 0px;
            cursor: pointer;
        }
    }

    .sample__code {
        margin: 0 0 10px;
        font-size: 13px;
        line-height: 1.42857143;
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #f1f1f1 !important;
        color: #323232 !important;
        padding: 12px;

        .markdown-body {
            padding-bottom: 0px;
        }

        .markdown-body .markdown_padding > p,
        .markdown-body .markdown_padding > blockquote {
            margin-bottom: 0px !important;
        }
    }

    .notebook_execute {
        line-height: 0;
        float: left;
        cursor: pointer;
        background-color: #1b2631;
    }

    .executable__code {
        color: #fff;
        background-color: #000;
        margin: 0 0 10px;
        font-size: 13px;
        line-height: 1.42857143;
        border: 1px solid #ccc;
        border-radius: 4px;
    }

    .error {
        color: #fff;
        background-color: #212f3c;
        margin: 0 0 10px;
        font-size: 13px;
        line-height: 1.42857143;
        padding: 14px;
        // border-left: 6px solid red;
    }

    .success {
        color: #fff;
        background-color: #212f3c;
        margin: 0 0 10px;
        font-size: 13px;
        line-height: 1.42857143;
        padding: 14px;
        // border-left: 6px solid #7fff00;
    }

    div > pre.success {
        border-left: 6px solid #7fff00;
        border-radius: 0px;
    }

    div > pre.error {
        border-left: 6px solid red;
        border-radius: 0px;
    }

    .bx--text-input {
        cursor: pointer;
        color: #fff;
        background-color: #1b2631;
        border-bottom: none;
    }

    .executable__notebookcode {
        color: #fff;
        margin: 0 0 0px;
        font-size: 13px;
        line-height: 1.42857143;
        border-radius: 0px;
        padding: 0px;
        border-bottom: 1px solid #8d8d8d;
    }

    .executable__notebookcode input.execute {
        cursor: pointer;
        color: #fff;
    }

    .executable__code code.execute {
        cursor: pointer;
        color: #7fff00;
    }

    .executable__code code.copycommand {
        cursor: pointer;
    }

    .copy_to_clipboard__code {
        display: flex;
        justify-content: space-between;
        overflow: visible;

        .copybutton {
            width: 1.5rem;
            height: 1.5rem;
            
            .bx--snippet__icon {
                width: 0.75rem;
                height: 0.75rem;
            }
        }
    }

    .markdown-body {
        font-family: "IBM Plex Sans", "Helvetica Neue", Arial, sans-serif !important;
        font-size: 15px !important;
        padding-bottom: 32px;

        .sample__code p {
            font-size: 13px !important;
            font-family: inherit;
        }
        p {
            font-size: 15px !important;
        }

        ol {
            list-style: decimal;
        }

        ul {
            list-style: disc;
        }

        .img_properties {
            cursor: pointer;
        }

        #sidebar {
            position: inherit !important;
        }

        #page-container {
            position: inherit !important;
        }
    }
}
