@import "~@carbon/themes/scss/themes";
$carbon--theme: $carbon--theme--g90;
@include carbon--theme();

.AccordionHeader {
  color: $text-01;
  background-color: #161616;
  padding: 1rem;

  div.catalog-page--link {
    height: 1.125rem; // gap to show when link is not there
  }

  h3 {
    font-size: 1.25em;
  }

  p {
    font-size: 15px;
  }

  .action-bar {
    margin-bottom: 1rem;
    display: flex;
    width: 100%;
  }

  svg.filledAlt {
    fill: $text-01;
  }
  
  .action-bar--align-right {
    display: flex;
    justify-content: flex-end;
    flex: 1 1;
    // height: 100%; 
  }
  .action-bar--container {
    margin-left: 1rem;
  }
}

